.productslist{
    width: 50%;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
}
.productslisthead{
    align-self: center;
    border-bottom: 2px solid black;
    margin: 50px 0 -80px 0;
}
.productslist table{
    margin-left: 0;
    width: 100%;
    padding: 10px;
}
.productslist thead{
    margin-bottom: 10px;
    font-size: 20px;
}
.AddnewButton{
    padding: 5px 18px;
    border: 2px solid black;
    background-color: black;
    color: white;
    font-size: 20px;
    font-weight: 600;
    border-radius: 22px;
    margin-right: 30px;
}
.editbutton{
    padding: 5px 18px;
    border: 2px solid black;
    background-color: black;
    color: white;
    font-size: 16px;
    font-weight: 600;
    border-radius: 22px
}
.deletebutton{
    padding: 5px 14px 5px 18px;
    border: 2px solid black;
    background-color: black;
    color: white;
    font-size: 16px;
    font-weight: 600;
    border-radius: 22px
}
.Productpop{
    width: 100%;
    height: 90%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.popclose{
    font-size: 20px;
    background-color: transparent;
    border: 0;
}
.Productpop h2{
    align-self: center;
}
.Productpop div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.Productpop label{
    font-size: 16px;
    font-weight: 600;
}
.Productpop input{
    padding: 5px;
    width: 70%;
}
.Productpop button{
    width: 50%;
    align-self: center;
    padding: 4px 12px;
    border-radius: 22px;
    background-color: black;
    color: white;
    font-size: 16px;
    font-weight: 600;
    margin-top: 10px;
}