.cartcontainer{
    width: 100vw;
    padding: 20px 50px;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 5px;
}
.cartflex{
    background-color: aliceblue;
    display: flex;
    flex-direction: row;
    border-radius: 22px;
}
.cartflex img{
    border-radius: 22px 0 0 22px;
}
.cartright{
    display: flex;
    flex-direction: column;
    align-content: space-between;
    align-items: center;
    width: 100%;
    background-color: yellow;
    border-radius: 0 22px 22px 0;

}
.carttop{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.carthead{
    width: 60%;
    padding-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cartdetail{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.cartdetailcolor{
    width: 60%;
    padding-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cartdetailcapacity{
    width: 60%;
    padding-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cartbottom{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: space-between;
}
.cartbottomquantity{
    width: 60%;
    padding-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cartbottomtotal{
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    padding-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cartflex button{
    margin-right: 0;
}
.cartBuyall{
    margin-top: 10px;
    width: 100%;
    padding: 25px 100px 25px 25px;
    display: flex;
    justify-content: flex-end;
    align-content: center;
}
.buythis{
    width: 100%;
}