.logbody{
   margin-top: -30;
   width: 100vw;
   height: 100vh;
   display: grid;
   justify-content: center;
   align-items: center;
   background-image: url('../assets/regmain bg.jpg');
}
.logpage{
    margin:-500px;
    height: 500px;
    border-radius: 20px;
   display: grid;
   grid-auto-flow: column;
   grid-auto-columns: 40% 60%;
   justify-content: center;
   align-items: center;
   text-align: center;
   background-color: rgb(240, 240, 240);
   background-image: url('../assets/regbgrv.jpg');

}
.logpageleft{
    height: 100%;
    background-color: rgb(100, 202, 168);
    display: flex;
    flex-direction: column;
    padding: 20px 0;
    justify-content: center;
    border-radius: 20px 0 0 20px;
   background-image: url('../assets/regbg.jpg');
}
.logpageleft h1{
    margin-bottom: 40px;
}
.logpageleft p{
    padding: 0 40px;
    margin-bottom: 40px;
}
.logpageleft button{
    width: 50%;
    margin-left: 25%;
    background-color: rgba(0,0,0,0);
    border: 2px solid rgba(0, 0, 0, 0.6);
    border-radius: 24px;
    padding: 10px 0;
    font-size: 16px;
    font-weight: 550;
    margin-top: 15px;
}

.logpageright{
}
.log-container h1{
    padding: 10px 0;
}
.logform{
    margin: 20px 0;
}
.logform input{
    width: 70%;
    padding: 18px 10px 15px 20px;
    margin: 20px 0;
    border: 0;
    font-size: 16px;
    border-radius: 25px;
    background-color: rgb(230, 230, 230);
}
.logform button{
    width: 30%;
    padding: 12px 0;
    font-size: 16px;
    color: rgba(255, 255, 255 ,0.8);
    font-weight: 550;
    margin-top: 25px;
    background-color: rgb(19, 97, 71);
    border: 0;
    border-radius: 24px;
}