.navcontainer{
    width: 100%;
    display: grid;
    grid-auto-flow: row;
    background-color: aliceblue;
}


.navtop{
    width: 100vw;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
.searchbar{
    position: relative;
}
.searchbar img{
    height: 1.5rem;
    margin-left: -28px;
    margin-bottom: -5px;
}
.searchbar input{
    padding: 2px 25px 5px 0px;
    font-size: 18px;
    box-sizing:border-box;
    border: 0;
    border-bottom: 2px solid black;
    background-color: rgba(255, 255, 255, 0);
    outline: none;
}
.header{
    background-size: cover;
    color: transparent;
    background-image: url(../assets/headbg.webp);
    background-clip: text;
    font-size: 60px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 800;
}
.navicons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.navicons img{
    width: 2rem;
    margin-right: 20px;
}
.navicons p{
    padding-top: 5px;
    font-size: 14px;
}
.navicons span{
    font-size: 12px;
    font-weight: lighter;
    color: rgb(255, 153, 0);
}
.loginicon{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.profileicon{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 20px;
}
.profileimg{
    border-radius: 50px;
}
.profileicon p{
    margin-left: -12px;
    margin-right: 10px;
}

.navbottom{
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(95, 95, 95);
    padding: 3px 0px;
}
.navbottom *{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.5);
    font-size: 22px;
    padding-left: 0px 5px;
}
.navbottom *.active{
    color: rgb(214, 155, 78);
    font-weight: bolder;
}


.logoutpage{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-content: center;
    align-items:center
}
.usericon{
    width: 4rem;
    height: 4rem;
    margin-top: 20px;
    margin-bottom: 10px;
    border-radius: 50%;
    background-color: blue;
}
.userdetails{
    width: 70%;
    margin-right: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: flex-start;
}
.userdetails *{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.buttoncontainer{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    align-items:center;
}
.buttoncontainer *{
    padding: 5px 10px;
    border-radius: 18px;
    margin: 10px;
}