.TablesContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: aliceblue;
}
.Tables{
    width: 100vw;
    height: 90vh;
    padding: 50px 80px;
    display: grid;
    grid-template-columns: auto auto auto auto ;
    grid-gap: 40px;
    justify-content: space-around;
    overflow-x: hidden;
    /* background-color: white; */
}
.flexcontainer{
    background-color: rgb(95, 95, 95);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 24px;
    box-shadow: 5px 5px rgba(64, 64, 64, 0.7) ;
}
.flexcontainer img {
    border-radius: 22px 22px 0 0;
}
.flexcontainer *{
    margin: 0;
    padding-bottom: 5px;
}
.show{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.top{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-top: -20px;
}
.top button{
    font-size: 24px;
    font-weight: 600;
    background-color: transparent;
    border: 0;
}
.favorite img{
    width: 50px;
}
.show img{
    border-radius: 22px;
}
.bottom{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.bottom button{
    padding: 5px 10px;
    border: 2px solid black;
    border-radius: 22px;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    color: black;

}