.userlistmain {
    display: flex;
    flex-direction: column;
    width: 500px;
}
.userlistmain h1{
    align-self: center;
    color: rgb(100, 100, 100);
    border-bottom: 2px solid rgb(0, 0, 0);
}
.userlistbutton{
    width: 30%;
    align-self: center;
    padding: 5px 15px;
    border: 0;
    border-radius: 28px;
    color: rgb(0, 0, 0);
    background-color: rgb(214, 214, 214);
    font-weight: 600;
    border: 2px solid rgb(0, 0, 0);
}
.userlistitems{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 28px;
    margin-bottom: 10px;
    color: rgb(0, 0, 0);
    background-color: rgb(214, 214, 214);
    font-size: 16px;
    font-weight: 600;
    margin-left: -39px;
}
.userlistIcon{
    width: 2rem;
    height: 2rem;
    border-radius: 25px;
    background-color: black;
    color: white;
    text-align: center;
    padding-top: 6px;
    font-size: 18px;
    font-weight: 700;
}
.userlistitems button{
    padding: 5px 15px;
    border: 0;
    border-radius: 22px;
    color: white;
    background-color: black;
    font-weight: 600;
}