.adminPage{
    margin: 0;
    width: 100vw;
    display: flex;
    flex-direction: row;
    overflow-x: hidden;
}
.adminNav{
    position: fixed;
    width: 20vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(50, 50, 50);
}
.adminNav img{
    width: 3rem;
    padding: 2px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 25px;
    margin-bottom: 20px;
    background-color: rgb(200, 200, 200);
}
.adminNav button{
    width: 50%;
    margin-bottom: 20px;
    padding: 8px 0 ;
    border: 2px solid black;
    border-radius: 16px;
    font-size: 18px;
    font-weight: 600;
}
.adminCompos{
    margin-left: 20vw;
    width: 80vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
}
.adminlog{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.adminlog button{
    padding: 4px 12px;
    border-radius: 22px;
    background-color: black;
    color: white;
    font-size: 16px;
    font-weight: 600;
    margin-top: 10px;
}