.paymentcontainer{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    color: rgb(0, 0, 0);
    /* background-color: yellow; */
}
table{
    margin: 100px 25%;
    width: 50%;
    height: min-content;
}
caption{
    padding-bottom: 10px;
    border-bottom: 2px solid white;
}
thead *{
    padding: 10px 0;
    text-align: left;
}
tbody *{
    padding: 0 0 10px 0;
    text-align: left;
}
.total{
    text-align: right;
}

tfoot *{
    padding-top: 10px;
    border-top: 2px solid white;
    /* border-bottom: 2px solid white; */
}
tfoot th{
    text-align: right;
}
tfoot td{
    text-align: right;
}

.buttonpay{
    margin: 0 25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.buttonpay button{
    width: 200px;
    border-radius: 12px;
    padding: 5px;
    font-weight: 500;
}