.regmain{
    background-image: url('../assets/regmain\ bg.jpg');
    height: 100vh;
    display: grid;
    justify-content: center;
    /* align-items: center; */
}
.regpage{
    /* background-image: linear-gradient(to right, rgb(0, 0, 91) , rgb(56, 53, 255) , rgb(96, 101, 255) , rgb(90, 90, 255)); */
    background-image: url('../assets/regbg.jpg');
    padding: 0;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 30% auto;
    text-align: center;
    margin: 150px 0;
    overflow: hidden;
    border-radius: 22px;
}


.regpageleft{  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: space-between;
    padding: 50px 0;
}
.regpagelefttop{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.regpageleftbottom{
    margin-top: 50px;
    text-align: center;
}
.loginbtn{
    padding: 8px 50px;
    border: 2px solid black;
    border-radius: 24px;
}


.regpageright{
    border-radius: 60% 0% 0% 60% ;
    background-image: url('../assets/regbgrv.jpg');
}
.reg-container{
    display: flex;
    flex-direction: column;
}
.regform{
    padding: 30px 20px;
    display: grid;
    grid-template-columns: 35% 35%;
    gap: 30px 50px;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.regform input{
    padding: 12px 10px;
    font-size: 16px;
}
.regform button{
    background-color: rgb(19, 97, 71);
    color: white;
    padding: 12px 0px; 
    border: 0;
    border-radius: 28px;
    font-size: 14px;
    width: 50%;
    margin-left: 50%;
    text-align: center;
}
.regLogin{
    display: none;
}
.regspinner::-webkit-inner-spin-button,
.regspinner::-webkit-outer-spin-button{
    -webkit-appearance: none;
}

@media only screen and (max-width: 980px) {
    body{
        padding: 0;
        margin: 0;
    }
    .regpage{
        display: flex;
        padding: 0 100px;
        margin: 0;
        flex-direction: row;
    }
    .regpageleft{
        display: none;
    }
    .regpageright{
        border-radius: 0 ;
        width: 100%;
        margin: 40px 0;
        background-color: rgba(0,0,0,0);
        background-image: none
    }
    .reg-container h1{
        font-size: 30px;
    }
    .regform{
        display: grid;
        grid-template-columns: 100%;
        justify-content: center;
        align-items: center;
        align-content: center;
    }
    .regform button {
        margin-left: 25%;
    }
    .regLogin{
        display: block;
    }
}

@media only screen and (max-width: 600px){
    .regpage{
        padding: 10px;
    }
    .regform{
        padding: 10px 10px;
    }
    .regpageright{
        background-color: rgba(0,0,0,0);

    }
    .regform input{
        background-color: rgba(0,0,0,0);
        border: 0;
        outline: 0;
        border-bottom: 2px solid rgb(0, 0, 0) ;
        color: white;
    }
}